//TabComponent.js
import React, { useState } from "react";
import styles from "./TabComponent.module.css";

const Tab = ({ id, title, isSelected, onClick, onKeyDown, textColor, tabBackground }) => (
  <button
    role="tab"
    id={id}
    aria-controls={`${id}-tabpanel`}
    aria-selected={isSelected}
    onClick={() => onClick(id)}
    onKeyDown={(event) => onKeyDown(id, event)}
    tabIndex={isSelected ? 0 : -1}
    style={{
      color: textColor,
      backgroundColor: isSelected ? tabBackground.selected : tabBackground.default,
    }}
  >
    {title}
  </button>
);

const TabPanel = ({ id, isSelected, children, tabIndex }) => (
  <div
    role="tabpanel"
    id={`tabpanel-${id}`}
    aria-labelledby={`tab-${id}`}
    hidden={!isSelected}
    tabIndex={tabIndex}
  >
    {children}
  </div>
);

export const TabComponent = ({
  tabsA,
  tabPanelTabindex,
  triggerActivation = "automated",
  textColor = "rgba(0, 0, 0, 1)",
  tabBackground = {
    default: "rgba(209, 203, 219, 1)",
    selected: "rgba(156, 39, 176, 1)",
  },
  ariaLabel = "HTML and ARIA information",
}) => {
  const [selectedTab, setSelectedTab] = useState(tabsA[0].id);

  const handleKeyDown = (id, event) => {
    const currentIndex = tabsA.findIndex((tab) => tab.id === id);
    
        if (event.key === "ArrowRight") {
          event.preventDefault();
          const nextIndex = (currentIndex + 1) % tabs.length;
          if (triggerActivation === "automated") {
            setSelectedTab(tabsA[nextIndex].id);
          }
          document.getElementById(tabsA[nextIndex].id).focus();
        } else if (event.key === "ArrowLeft") {
          event.preventDefault();
          const prevIndex = (currentIndex - 1 + tabsA.length) % tabsA.length;
          if (triggerActivation === "automated") {
            setSelectedTab(tabsA[prevIndex].id);
          }
          document.getElementById(tabsA[prevIndex].id).focus();
        } else if (event.key === "Home") {
          event.preventDefault();
          if (triggerActivation === "automated") {
            setSelectedTab(tabsA[0].id);
          }
          document.getElementById(tabsA[0].id).focus();
        } else if (event.key === "End") {
          event.preventDefault();
          if (triggerActivation === "automated") {
            setSelectedTab(tabsA[tabsA.length - 1].id);
          }
          document.getElementById(tabsA[tabsA.length - 1].id).focus();
        } else if (
          (event.key === "Enter" || event.key === " ") &&
          triggerActivation === "manual"
        ) {
          event.preventDefault();
          setSelectedTab(id);
        }
      };

  return (
    <div className={styles.tabContainer}>
    <div role="tablist" aria-label="HTML and ARIA information" className={styles.tabList}>
        {tabsA.map(({ id, title }) => (
          <Tab
            key={id}
            id={id}
            title={title}
            isSelected={selectedTab === id}
            onClick={() => setSelectedTab(id)}
            onKeyDown={handleKeyDown}
            textColor={textColor}
            tabBackground={tabBackground}
          />
        ))}
      </div>
      {tabsA.map(({ id, content }) => (
        <TabPanel
          key={id}
          id={id}
          isSelected={selectedTab === id}
          tabIndex={tabPanelTabindex ? 0 : undefined}
        >
          {content}
        </TabPanel>
      ))}
    </div>
  );
};